.navbar {
    width : 100vw;
    height : 10vh;
    position: fixed;
    background-color: #af1730;
    display: flex;
    z-index: 999;

    .img {
        width : 14vh;
        height : 10vh;
        background-image: url('./img1.png');
        background-size: 100%;
    }

    h1 {
        color: white;
        font-size : 1.3rem;
        align-self: center;
        cursor: pointer;
    }

    .elements {
        display: flex;
        width : 40vw;
        justify-content: flex-end;
        transform: translateX(17vw);
        h1 {
            font-size : 1.15rem;
            margin-right : 10vw;
            transition: transform 0.5s;
            cursor: pointer;
        }
        h1::after {
            content: '';
            width : 100%;
            height : 0.5vh;
            background-color: white;
            display: block;
            transform: scaleX(0);
            transition: 0.45s;
        }
        h1:hover::after {
            transform: scaleX(1);
        }
        h1:hover {
            transform: translateY(-0.75vh);
        }
    }
    .elements2 {
        @extend .elements;
        justify-content: flex-start;
    }

}
@media screen and (min-width : 1800px) {
    .navbar {
        h1 {
            font-size : 2rem;
        }

        .elements {
            h1 {
                font-size : 1.5rem;
            }
        }
    }
}