.home{
    video {
        filter: blur(5px) sepia(0.6);
        height : 100vh;
        background-color: black;
        position: fixed;
        z-index : -1;
    }
    div {
        padding-left : 5vw;
        padding-top : 42vh;
        h1 {
            margin : 0;
            font-size : 5rem;
            color: #af1730;
        }
        h2 {
            color: #af1730;
            font-size : 1.5rem;
        }
        button {
            width : 15vw;
            height : 9vh;
            margin-top : 4vh;
            font-size : 1.5rem;
            border: none;
            color : white;
            background-color: #af1730;
            outline: none;
            cursor: pointer;
            will-change: background-color;
            transition: background-color 0.6s;
        }
        button:hover {
            background-color: #e40127;
        }
    }
}
@media screen and (min-width : 1800px) {
    .home {
        div {
            h1 {
                font-size : 8rem;
            }
            h2 {
                font-size : 3rem; 
            }
            button {
                font-size: 2rem;
            }
        }
    }
}