@import url('https://fonts.googleapis.com/css2?family=Caveat&family=Dancing+Script:wght@700&display=swap');
.union {
    width : 100vw;
    height : 100vh;
    background-color: wheat;
    margin-top : 13vh;
    transition: 0.6s;

    h1 {
        padding-top : 12vh;
        text-align: center;
        font-size: 3rem;
        font-family: 'Caveat', cursive;
        transition: 0.6s;
        margin: 0;
    }

    .content {
        width : 100vw;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        align-items: center;
        justify-content: center;
        height: 0%;
        transition: 0.6s;
        ul {
            width : 45vw;
            font-family: 'Caveat', cursive;
            font-size : 1.45rem;
            transition: 0.6s;
        }
        .smallerUl {
            font-size : 1.3rem;
        }
        p {
            text-align: center;
            max-width: 45vw;
            font-family: 'Caveat', cursive;
            font-size : 1.45rem;
            transition: 0.6s;
        }
        .p2 {
            font-size : 1.3rem; 
            max-width: 47vw;
        }
        button {
            width : 10vw;
            height : 5vh;
            align-self: center;
            justify-self: center;
            transform: translateY(-2vh);
            margin-left: 15vw;
            cursor: pointer;
            background-color: #af1730;
            color: white;
            border: none;
            font-size : 1.1rem;
            outline: none;
            transition: 0.6s;
        }
        .img1 {
            width : 40vw;
            background-image: url('./unnamed.jpg');
            height: 50vh;
            background-size: cover;
            justify-self: center;
            transition: 0.6s;
        }
        .img2 {
            @extend .img1;
            background-image: url('./img2.png');
        }
        .img3 {
            @extend .img1;
            background-image: url('./Prowincje_I_RP.svg.png');
            background-size: 70%;
            background-repeat: no-repeat;
            transform: translateX(5vw);
        }
        .img4 {
            @extend .img1;
            background-image: url('./img1.jpg');
        }
        .img5 {
            @extend .img1;
            background-image: url('./Coat_of_arms_of_Lithuania.svg');
            background-size: 55%;
            background-repeat: no-repeat;
            transform: translateX(5vw);
            height: 55vh;
        }
        .img6 {
            @extend .img1;
            background-image: url('./unnamed.jpg');
        }
        .img7 {
            @extend .img1;
            background-image: url('./potop.webp');
        }
        .img8 {
            @extend .img1;
            background-image: url('./wieden.png');
        }
        .img9 {
            @extend .img1;
            background-image: url('./Battle_of_Kliszow_1702.jpg');
        }
        .img10 {
            @extend .img1;
            background-image: url('./przysiega-tadeusza-kosciuszki-na-rynku-w-krakowie.jpg');
        }
    }

}
.union2 {
    @extend .union;
    margin : 0;
    margin-bottom: 0vh;
}

@media screen and (min-width : 1800px) {
    .union {
        height : 100vh;
        margin-top : 26vh;
        
        h1 {
            font-size : 5rem;
        }
        .content {
            margin-top : 0;
            ul {
                font-size : 2.15rem;
            }
            .smallerUl {
                font-size : 1.95rem;
            }
            p {
                font-size : 2.15rem;
            }
            .p2 {
                font-size : 1.95rem;
            }
        }
    }
    .union2 {
        margin-top : 0;
    }
}